<script>
export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  computed: {
    getDays() {
      let currentDate = new Date()
      let kukusyaDate = new Date("2022-12-31T08:00:00")
      console.log(currentDate, kukusyaDate)
      return Math.round((kukusyaDate.getTime() - currentDate.getTime()) / (1000 * 3600 * 24))
    }
  },
  mounted() {
    const script = document.createElement("script");
    script.setAttribute("src", "https://app.embed.im/snow.js");
    script.setAttribute("defer", true);
    document.body.insertAdjacentElement("beforeEnd", script);
  },
};
</script>

